@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
.main-navbar {
  font-family: Poppins, sans-serif;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  height: 70px;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background-color: white;
  z-index: 1000;
  transition: all 1s ease-in-out;
  background-color: transparent;
}

.navbar-shadow {
  -webkit-box-shadow: 8px 8px 24px 0px rgba(66, 68, 90, 1);
  -moz-box-shadow: 8px 8px 24px 0px rgba(66, 68, 90, 1);
  box-shadow: 8px 8px 24px 0px rgba(66, 68, 90, 1);
  background-color: white;
}

.navbar-logo {
  cursor: pointer;
  font-size: 24px;
  font-weight: 600;
  color: #333;
  .navbar-logo-first-element {
    color: #003366;
  }
  .navbar-logo-second-element {
    color: #ffa500;
  }
}

.navbar-menu {
  display: flex;
  gap: 20px;
  padding-right: 30px;
  font-weight: 500;
  font-size: 18px;
}

.navbar-menu-item {
  text-decoration: none;
  color: #ffa500;
  cursor: pointer;
  transition: color 0.4s ease-in-out;
}

.navbar-menu-item:hover {
  color: #666;
}

.hero {
  width: 100%;
  height: 100vh;
  padding: 0;
  margin: 0;
  position: relative;
  background-image: url('../../images/malaga-hero.png');
  display: flex;
  align-items: center;
  justify-content: center;

  .hero-menu {
    width: 100%;
    margin-top: 100px;
    display: flex;
    align-items: center;
    justify-content: space-around;

    .hero-menu-item {
      font-size: 30px;
      font-weight: 600;
      color: #ffa500;
      text-decoration: none;
      z-index: 2;
      position: relative;
      transition: color 0.4s ease-in-out;
    }
    .hero-menu-item:hover {
      cursor: pointer;
      color: #ffffff;
    }
  }
  .main-content {
    width: 100%;
    height: 50%;

    #hero-first-element {
      font-size: 50px;
      font-weight: 700;
      color: #ffa500;
      margin-bottom: 20px;
      z-index: 2;
      position: relative;
    }
    #hero-second-element {
      font-size: 30px;
      font-weight: 500;
      color: #ffa500;
      z-index: 2;
      position: relative;
    }
  }
}

.hero::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 51, 102, 0.5);
  z-index: 1;
}

.about {
  width: 100%;
}

.description {
  display: flex;
  flex-flow: column nowrap;
  align-items: start;
  justify-content: space-between;
  margin-top: 2vh;
  padding: 0 10% 0 10%;
}

.description-title {
  display: inline-block;
  width: 61%;
  color: #ffa500;
  font-size: 24px;
  font-weight: 500;
  text-align: start;
  border-bottom: 2px solid #003366;
}

.description-text-image {
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
  justify-content: space-between;
  gap: 10%;
  margin-top: 100px;
  &.reverse {
    flex-flow: row-reverse nowrap;
  }
  .description-text-wrapper {
    width: 100%;
  }
  & .description-text {
    width: 100%;
    color: #003366;
    font-size: 20px;
    font-weight: bold;
  }
}

.description-image {
  border-radius: 50px;
  width: 35vw;
  height: 20vw;
  object-fit: cover;

  -webkit-box-shadow: 0px 11px 22px -2px rgba(66, 68, 90, 1);
  -moz-box-shadow: 0px 11px 22px -2px rgb(47, 48, 57);
  box-shadow: 0px 11px 22px -2px rgba(66, 68, 90, 1);
}

.expectations {
  margin-top: 5vh;

  padding: 0;

  .description-title {
    margin-left: 1vw;
    margin-bottom: 5vh;
  }

  .description-content {
    display: flex;
    flex-flow: row nowrap;
    align-items: center;
    justify-content: space-between;
    z-index: 0;
    overflow: hidden;
    width: 100%;

    .expectations-control {
      z-index: 1;
      width: 100%;
      position: absolute;
      align-self: center;
      justify-self: unset;
      display: flex;
      flex-flow: row nowrap;
      align-items: center;
      justify-content: space-between;
      padding: 0 1vw;

      .left-arrow,
      .right-arrow {
        width: 3vw;
        fill: #003366;
      }
    }

    .expectations-list {
      padding: 1vh 1vw;
      display: flex;
      position: relative;
      flex-flow: row nowrap;
      align-items: center;
      justify-content: space-between;
      transition: left 1s;
    }
  }
}

.process {
  margin: 5vh 0;
  padding: 0;

  .description-title {
    margin-left: 1vw;
    margin-bottom: 5vh;
  }

  .description-content {
    width: 100%;
    display: flex;
    flex-flow: column nowrap;
    align-items: center;
    justify-content: center;
    padding: 0 3vw;

    table {
      box-shadow: 0px 0px 8px #003366;
      border: 1px solid #003366;
      border-collapse: collapse;
      width: 80vw;
      min-width: fit-content;

      thead {
        tr {
          background-image: linear-gradient(to bottom right, #003366, #0066cc);
        }
      }

      tr {
        border: 1px solid #003366;
        td {
          padding: 1vh 1vw;
          &:nth-of-type(2) {
            text-align: left;
          }
          &:nth-of-type(3) {
            padding: 1vh 1.5vw;
          }
        }

        th {
          padding: 1vh 1vw;
          color: #ffa500;
        }
      }

      tr:nth-child(even) {
        background-color: #cce6ff;
      }
    }

    .link-button {
      border: 1px solid #003366;
      padding: 1vh 1vw;
      display: flex;
      flex-flow: row nowrap;
      align-items: center;
      justify-content: center;
      text-decoration: none;
      text-decoration-line: none;
      background-image: linear-gradient(to bottom right, #003366, #0066cc);

      .label {
        color: #ffa500;
        margin-right: 1vw;
      }

      .icon {
        fill: #ffa500;
      }

      &:hover {
        background-image: none;
        background-color: #0066cc;

        .label {
          font-weight: 500;
        }

        .icon {
          font-weight: 500;
        }
      }
    }
  }
}

.attendees {
  margin: 5vh 0;
  padding: 0;

  .description-title {
    margin-left: 1vw;
    margin-bottom: 5vh;
    padding-bottom: 1vh;

    a {
      position: relative;
      top: 0px;
      margin-left: 1vw;
      padding: 0 0.5vw;
      transition: box-shadow 0.2s, top 0.2s;

      &:hover {
        top: -1px;
        box-shadow: 0px 0px 5px #003366;
      }

      &:active {
        top: 1px;
        box-shadow: none;
      }

      .download {
        height: 16px;
        fill: #ffa500;
      }
    }
  }

  .description-content {
    width: 100%;
    display: flex;
    flex-flow: column nowrap;
    align-items: center;
    justify-content: center;
    padding: 0 3vw;

    table {
      box-shadow: 0px 0px 8px #003366;
      border: 1px solid #003366;
      border-collapse: collapse;
      width: 80vw;
      min-width: fit-content;

      thead {
        tr {
          background-image: linear-gradient(to bottom right, #003366, #0066cc);
        }
      }

      tr {
        border: 1px solid #003366;
        td {
          padding: 1vh 1vw;
        }

        th {
          padding: 1vh 1vw;
          color: #ffa500;
        }
      }

      tr:nth-child(even) {
        background-color: #cce6ff;
      }
    }
  }
}
#oczekiwania {
  padding-bottom: 50px;
}
footer {
  display: flex;
  flex-flow: column nowrap;
  align-items: center;
  justify-content: space-evenly;
  background-image: linear-gradient(to top right, #003366 60%, #0066cc);
  padding: 2vh 0;

  .project-details-container {
    width: 100%;
    display: flex;
    flex-flow: column-reverse nowrap;
    align-items: center;
    justify-content: space-between;

    .project-details {
      color: white;
      margin-top: 4vh;
    }

    .partners {
      width: 100%;
      display: flex;
      flex-flow: row nowrap;
      align-items: center;
      justify-content: space-evenly;

      img {
        height: 12vh;
      }
    }
  }

  .authors-social-media {
    margin-top: 4vh;
    width: 100%;
    display: flex;
    flex-flow: row nowrap;
    align-items: center;
    justify-content: space-evenly;

    .authors {
      height: 5vh;
      color: white;

      display: flex;
      flex-flow: row nowrap;
      align-items: center;
      justify-content: space-evenly;
      flex-grow: 1;

      .closure {
        font-size: 10px;
        font-weight: 500;
      }
    }

    .social-media {
      width: 10vw;
      height: 5vh;
      display: flex;
      flex-flow: row nowrap;
      align-items: center;
      justify-content: center;
      margin-right: 2vw;

      .link {
        margin: 0 0.5vw;
        .fb,
        .youtube {
          height: 5vh;
          fill: white;
        }
      }
    }
  }
}
